body {
  background-color: #eee;
}

.App {
  font-family: Roboto, Arial, sans-serif;
}

.header-banner {
  background-color: #007a87;
  width: 100%;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  margin-bottom: 50px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.beach-content, .header-content {
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}

.rotate {
  animation: rotation 2s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

h1 {
  text-transform: uppercase;
  color: white;
  display: inline-block;
}

h2 {
  text-transform: uppercase;
  margin-top: 100px;
}

h3 {
  margin-top: 50px;
}

.good {
  color: green;
}

.fair {
  color: orange;
}

.poor {
  color: red;
}

.na {
  color: grey;
}

p {
  line-height: 1.5em;
}

p .updated-at-text {
  margin: 0;
}

.updated-at-text {
  font-weight: 300;
  font-size: 0.7em;
}

.select-container {
  margin-top: 100px;
  width: 100%;
}

select {
  margin-top: 10px;
  display: block;
  width: 100%;
  height: 3em;
  border-style: none;
  border-bottom: solid 1px #007a87;
  border-radius: 0px;
  background-color: transparent;
  font-size: 1.5em;
  font-weight: 700;
  box-sizing: border-box;
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
  appearance: none;
	background-image: url('./br_down.png');
	background-repeat: no-repeat;
	background-position: right .7em top 50%;
	background-size: .65em auto;
}

input:focus, *:focus {
  outline: none;
}

@media (min-width: 400px) {
  .beach-content, .header-content {
    width: 400px;
    margin: auto;
    padding: 0 10px;
    box-sizing: border-box;
  }

}
